import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { GroupGradeInfoBean } from './../bean/custom-group.bean';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'app-group-rating',
  templateUrl: './group-rating.component.html',
  styleUrls: ['./group-rating.component.scss'],
  providers: [ConfirmationService,CommonHttpService,Commfunc],

})
export class GroupRatingComponent implements OnInit {

  public cunstomGroupName: string = "XXX客户群";
  public cunstomRatingAdjust: string = "群信用星级调整";
  public cunstomRating: string = "群信用星级";
  public msgs: Message[] = [];  //提示信息
  public groupGradeInfoBean: GroupGradeInfoBean = new GroupGradeInfoBean();// 客户群评级查询
  public groupAfterLevel: string;//调整星级
  public rateFlag: boolean = true;//是否已评级，默认已评级
  public selGroupLevel: any;//当前星级
  // 管道码值
  // public CODEVALUE: any =  CODEVALUE;
  // 控件
  sysGroupLevel: SelectItem[] = [];//系统星级
  groupLevel: SelectItem[] = [];//当前星级
  groupLevel22: SelectItem[] = [];//当前星级
  adjustGroupLevel: SelectItem[] = [];//调整星级
  // 链接中间量
  public groupId: any = '';//群号
  public groupName: any = '';//群名称


  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
    public CommonHttpService: CommonHttpService,
    public Commfunc: Commfunc,
  ) { }

  ngOnInit() {
    let session = window.sessionStorage;
    if(session.getItem('para')){
      session.removeItem('para')
    }
    this.preSet();
    this.selectGroupGradeInfo();
  }
  preSet() {
    // 初始化
    // route传参
    if (this.routerInfo.snapshot.params['id']) {
      this.groupId = this.routerInfo.snapshot.params['id'];//群id      
    }
    if (this.routerInfo.snapshot.params['name']) {
      this.groupName = this.routerInfo.snapshot.params['name'];//群名称      
    }
    if (this.routerInfo.snapshot.params['groupId']) {
      this.groupId = this.routerInfo.snapshot.params['groupId'];//群名称      
    }
    if (this.routerInfo.snapshot.params['groupName']) {
      this.groupName = this.routerInfo.snapshot.params['groupName'];//群名称      
    }
    this.groupGradeInfoBean.groupId = this.groupId
    this.groupGradeInfoBean.groupName = this.groupName
    this.cunstomGroupName = this.groupGradeInfoBean.groupName + '客户群';//标题
    // this.groupId = '0003';//群id假数据

    // 当前群评星级,调整返回
    this.groupLevel22 = [];
    this.groupLevel22.push({ label: '请选择', value: '' });
    this.groupLevel22.push({ label: '一星', value: '1' });
    this.groupLevel22.push({ label: '二星', value: '2' });
    this.groupLevel22.push({ label: '三星', value: '3' });
    this.groupLevel22.push({ label: '四星', value: '4' });
    this.groupLevel22.push({ label: '五星', value: '5' });   
    
    // 
    this.groupLevel = [];
    this.groupLevel.push({ label: '请选择', value: '' });
    this.groupLevel.push({ label: '三星', value: '3' });
    this.groupLevel.push({ label: '四星', value: '4' });
    this.groupLevel.push({ label: '五星', value: '5' });
    // // 系统建议星级
    // this.sysGroupLevel = [];
    // // this.sysGroupLevel.push({ label: '请选择', value: '' });
    // this.sysGroupLevel.push({ label: '一星', value: '1' });
    // this.sysGroupLevel.push({ label: '二星', value: '2' });
    // this.sysGroupLevel.push({ label: '三星', value: '3' });
    // this.sysGroupLevel.push({ label: '四星', value: '4' });
    // this.sysGroupLevel.push({ label: '五星', value: '5' });
    // 调整星级
    this.adjustGroupLevel = [{ label: '请选择', value: '' }];
    this.adjustGroupLevel.push({ label: '三星', value: '3' });
    this.adjustGroupLevel.push({ label: '四星', value: '4' });
    this.adjustGroupLevel.push({ label: '五星', value: '5' });

    // 调试用
    // this.rateFlag = false;
  }
  selectGroupGradeInfo() {
    // 客户群评级查询
    this.httpService.selectGroupGradeInfo(this.groupGradeInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        //传参
        
        // 查询修改
        if (data.createFlag == '0') {
          this.rateFlag = true;
          this.groupGradeInfoBean = data;
        }
        // 新增
        if (data.createFlag == '1') {
          this.rateFlag = false;
          this.groupGradeInfoBean = data;
        }
        this.groupGradeInfoBean = data;
        this.selGroupLevel = data.groupLevel;//当前星级

        this.queryTellerById(data)

      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  sysGroupLevelSel() {
    // 客户评级调整返回方法
  }
  submit_GroupGradeInfo() {
    // 客户评级提交（修改）
    let flag1 = true;
    flag1 = this.isNull1();
    if (!flag1) {
      return;
    }
    this.confirmationService.confirm({
      message: '确定提交吗?',
      accept: () => {
        this.groupGradeInfoBean.groupAfterLevel = this.groupAfterLevel;
        this.httpService.updateGroupGradeInfo(this.groupGradeInfoBean).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.groupAfterLevel = '';
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '客户评级修改审批提交成功！' });
            this.selectGroupGradeInfo();
          } else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '调用服务失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        });
      }
    });
  }
  addGroupGradeInfo() {
    // 新增
    let flag2 = true;
    flag2 = this.isNull2();
    if (!flag2) {
      return;
    }
    this.confirmationService.confirm({
      message: '确定提交并返回客户群页面?',
      accept: () => {
        this.groupGradeInfoBean.groupLevel = this.selGroupLevel;//当前星级
        this.groupGradeInfoBean.sysGroupLevel = '1';
        let param = this.groupGradeInfoBean;
        this.httpService.addGroupGradeInfo(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
            this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
          } else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '新增失败';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }]
          }
        }, err => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
      }
    });
  }
  isNull1() {
    if (!this.groupAfterLevel || this.groupAfterLevel == ' ') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调整后的信用星级不能为空' });
      return false;
    }
    return true;
  }
  isNull2() {
    if (!this.selGroupLevel || this.selGroupLevel == ' ') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '信用星级不能为空' });
      return false;
    }
    return true;
  }
  // 链接
  returnGroupView() {
    // 返回客户群视图
    this.confirmationService.confirm({
      message: '确定返回客户群页面?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
      }
    });
  }
  toAppHistory() {
    //跳转到群评级历史
    this.confirmationService.confirm({
      message: '确定跳转到群评级历史页面?',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/application-history', { groupId: this.groupId, groupName: this.groupName }]);
      }
    });
  }
   //查询人员姓名
   queryTellerById(item){
    let params = {
      tellerId : item.createUserId    
    }
    let successFnc = data => {
      let orgName = '';
      data.relationshipList.forEach(item2 => {
        item2.orgId == item.createOrgId ? orgName = item2.orgName : '';
      });
      this.groupGradeInfoBean["createUserName"] = data.tellerName;
      this.groupGradeInfoBean["createOrgName"] = orgName;
      
    }
    this.Commfunc.interfaceComFnc(this.CommonHttpService.queryTellerById(params),successFnc,this.msgs)
  }
}
